@import url(https://fonts.googleapis.com/css2?family=Raleway:wght@100;200;300;400;500;600;700;800&display=swap);
/* base styles */
body {
  font-family: Raleway, sans-serif;
  margin: 0;
  font-size: 1.5em;
  text-align: center;
  background: #1b1523;
  color: #fff;
}

.App {
  max-width: 700px;
  /* max-width: 860px; */
  margin: 1px auto;
}

.App h1 {
  font-family: 'HG行書体',
    'HGP行書体',
    'HGS行書体',
    'HGP教科書体',
    'ＭＳ 明朝',
    'Script',
    serif;
  font-size: 2rem;
  margin-top: 5px;
  margin-bottom: 5px;
}

button {
  background: #444;
  border: 2px solid rgb(236, 132, 62);
  padding: 2px 10px;
  border-radius: 4px;
  color: #fff;
  font-weight: bold;
  cursor: pointer;
  font-size: .75em;
  margin-bottom: 0px;
}

button:hover {
  background: #c23866;
  color: #fff;
}

.card-grid {
  margin: 20px 10px 0 10px;
  display: grid;
  /* grid-template-columns: 1fr 1fr 1fr 1fr; */
  grid-template-columns: minmax(55px, 1fr) minmax(55px, 1fr) minmax(55px, 1fr) minmax(55px, 1fr);
  /* grid-template-columns: repeat(auto-fill, minmax(55px, 1fr)); */
  grid-gap: 20px;
}

#orderB {
